<template>
  <div>
    <el-dialog
      title="标题"
      :visible.sync="dialogVisible"
      width="50%">
      <el-form :model="formModel">
        <el-form-item label="分类名称">
          <el-input v-model="formModel.cate_name"></el-input>
        </el-form-item>

        <el-form-item label="分类别名">
          <el-input v-model="formModel.cate_alias"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { addNewChannel, updatedChannel } from '@/api/article'

export default {
  data () {
    return {
      dialogVisible: false,
      formModel: {
        id: '',
        cate_name: '',
        cate_alias: ''
      }
    }
  },
  methods: {
    open (row) {
      if (row.id) {
        // 编辑
        this.dialogVisible = true
        this.formModel = row
      } else {
        // 添加
        this.dialogVisible = true
      }
    },

    // 提交
    async onSubmit () {
      this.dialogVisible = false
      if (this.formModel.id) {
        // 提交编辑
        await updatedChannel(this.formModel)
        this.$emit('success', 'edit')
      } else {
        // 提交添加
        await addNewChannel({ cate_name: this.formModel.cate_name, cate_alias: this.formModel.cate_alias })
        this.$emit('success', 'add')
      }
    }
  },
  watch: {
    dialogVisible (val) {
      if (val === false) {
        this.formModel = {
          id: '',
          cate_name: '',
          cate_alias: ''
        }
      }
    }
  }
}
</script>

<style scoped></style>
