// 文章
import $http from '@/utile/$user.js'

// 获取文章分类列表
export const getCategoryList = () => $http.get('/my/cate/list')

// 添加文章分类
export const addNewChannel = (data) => $http.post('/my/cate/add', {
  ...data
})

// 编辑文章分类
export const updatedChannel = (data) => $http.put('/my/cate/info', {
  ...data
})

// 删除文章分类
export const deleteChannel = (id) => $http.delete('/my/cate/del', {
  params: {
    id
  }
})

// 文章
// 获取文章列表
export const getArticleList = (data) => $http.get('/my/article/list', {
  params: data
})

// 获取文章详细
export const getArticleDetail = (id) => $http.get('/my/article/info', {
  params: {
    id
  }
})

// 添加文章
export const addNewARticle = (data) => $http.post('/my/article/add', data)

// 编辑文章
export const editArticle = (data) => $http.put('/my/article/info', data)

// 删除文章
export const deleteArticle = (id) => $http.delete('/my/article/info', {
  params: { id }
})
