<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <Headercomponer title="文章分类">
      <!-- 标题及操作区 -->
      <div slot="extra">
        <el-button  @click="handleAdd" type="success">+添加分类</el-button>
      </div>

      <!-- 主要内容 -->
      <div>
        <!-- 列表 -->
        <el-table :data="pageData" max-height="880px" v-loading="loading">
          <el-table-column
            label="分类ID"
            prop="id"
            width="auto">
          </el-table-column>

          <el-table-column
            label="分类名称"
            prop="cate_name"
            width="auto">
          </el-table-column>

          <el-table-column
            label="分类别称"
            prop="cate_alias"
            width="auto">
          </el-table-column>

          <el-table-column
            fixed="right"
            label="操作"
            width="110">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.row)" type="success" icon="el-icon-edit" circle></el-button>
              <el-button @click="handleDelt(scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页 -->
        <el-pagination
          @size-change="handleChangeLamit"
          @current-change="handleChangePage"
          :current-page="page"
          :page-sizes="[10, 20, 25, 35]"
          :page-size="10"
          layout="total, sizes, prev, pager, next"
          :total="total">
        </el-pagination>

        <!-- 弹窗 -->
        <ChannleAlter ref="channleAlter" @success="onSuccess"></ChannleAlter>
      </div>
    </Headercomponer>
  </div>
</template>

<script>
import { getCategoryList, deleteChannel } from '@/api/article.js'
import Headercomponer from '@/components/headercomponer.vue'
import ChannleAlter from '@/components/channleAlter.vue'
import { Message } from 'element-ui'
export default {
  data () {
    return {
      loading: false,
      channleList: [],
      page: 1,
      lamit: 10
    }
  },
  methods: {
    // 获取文章分类列表
    async getChannleList () {
      this.loading = true
      await getCategoryList()
        .then((res) => {
          this.channleList = res.data.reverse()
          this.loading = false
        })
    },
    // 修改显示条数时
    handleChangeLamit (show) {
      this.page = 1
      this.lamit = show
      this.getChannleList()
    },
    // 修改页码时
    handleChangePage (page) {
      this.page = page
      this.getChannleList()
    },
    // 添加
    handleAdd () {
      this.$refs.channleAlter.open({})
    },
    // 编辑
    handleEdit (row) {
      this.$refs.channleAlter.open(row)
    },
    // 删除
    handleDelt (row) {
      this.$confirm('您确定需要删除该分类么？', '温馨提示', {
        confirmButtonText: '删除',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await deleteChannel(row.id)
          this.getChannleList()
        })
        .catch(() => {})
    },

    // 操作成功
    onSuccess (val) {
      if (val === 'add') {
        Message.success('添加成功')
      }
      if (val === 'edit') {
        Message.success('编辑成功')
      }
      this.getChannleList()
    }
  },
  computed: {
    // 分页
    pageData () {
      const start = (this.page - 1) * this.lamit
      const end = start + this.lamit
      return this.channleList.slice(start, end)
    },
    // 总数
    total () {
      return this.channleList.length
    }
  },
  created () {
    this.$store.commit('tag/selecMenu', {
      path: this.$route.path,
      label: '文章分类'
    })

    // 调用获取文章分类接口
    this.getChannleList()
  },
  components: {
    Headercomponer,
    ChannleAlter
  }
}
</script>

<style lang="less" scoped></style>
